import React, { useState } from 'react';
import { CognitoUserPool, CognitoUserAttribute } from 'amazon-cognito-identity-js';
import { Link } from '@material-ui/core';

const Register = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const poolData = {
    UserPoolId: 'us-east-1_EeXlDsQ8J',
    ClientId: '4d1k0aettnaamj3dtbknl5cdq1'
  };
  const userPool = new CognitoUserPool(poolData);

  const handleSubmit = (event) => {
    event.preventDefault();
    const emailAttribute = new CognitoUserAttribute({
      Name: 'email',
      Value: email,
    });

    userPool.signUp(email, password, [emailAttribute], null, (error, result) => {
      if (error) {
        console.error('Signup error:', error);
        
      } else {
        console.log('Signup successful:', result);
        <Link to="/login"></Link>
      }
    });
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
        Email:
        <input type="email" value={email} onChange={(event) => setEmail(event.target.value)} />
      </label>
      <br />
      <label>
        Password:
        <input type="password" value={password} onChange={(event) => setPassword(event.target.value)} />
      </label>
      <br />
      {/* <button type="submit">Signup</button> */}
      <button type="button" onClick={(e) => {e.preventDefault(); window.location.href="/login"}}>Sign Up</button>
    </form>
  );
};

export default Register;


